<template>
  <div id="login">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="main">
            <Icon name="logo" />
          </div>
          <div class="form">
            <h3 @click="showRegister">创建账户</h3>
            <div v-show="isShowRegister" class="register">
              <input
                type="text"
                v-model="register.username"
                placeholder="用户名"
              />
              <input
                type="password"
                v-model="register.password"
                placeholder="密码"
              />
              <p :class="{ error: register.isError }">{{ register.notice }}</p>
              <div class="button" @click="onRegister">创建账号</div>
            </div>
            <h3 @click="showLogin">登录</h3>
            <div v-show="isShowLogin" class="login">
              <input
                type="text"
                v-model="login.username"
                placeholder="输入用户名"
              />
              <input
                type="password"
                v-model="login.password"
                placeholder="密码"
              />
              <p :class="{ error: login.isError }">{{ login.notice }}</p>
              <div class="button" @click="onLogin">登录</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Auth from "@/apis/auth";

import Bus from "@/helpers/bus";
//在开始的时候获取登录状态
Auth.getInfo().then((data) => {
  console.log(data);
});
export default {
  data() {
    return {
      isShowLogin: true,
      isShowRegister: false,
      login: {
        username: "",
        password: "",
        notice: "输入用户名和密码",
        isError: false,
      },
      register: {
        username: "",
        password: "",
        notice:
          "用户名1~15个字符，仅限于字母、数字、下划线、中文；密码长度为6~16个字符",
        isError: false,
      },
    };
  },
  methods: {
    showLogin() {
      this.isShowLogin = true;
      this.isShowRegister = false;
    },
    showRegister() {
      this.isShowLogin = false;
      this.isShowRegister = true;
    },
    //注册框
    onRegister() {
      if (!/^[\w\u4e00-\u9fa5]{1,15}$/.test(this.register.username)) {
        this.register.isError = true;
        this.register.notice = "用户名1~15个字符，仅限于字母数字下划线中文";
        return;
      }
      if (!/^.{6,16}$/.test(this.register.password)) {
        this.register.isError = true;
        this.register.notice = "密码长度为6~16个字符";
        return;
      }
      this.register.isError = false;
      this.register.notice = "";
      Auth.register({
        username: this.register.username,
        password: this.register.password,
      })
        .then((data) => {
          this.register.isError = false;
          this.register.notice = "";
          this.$router.push({ path: "notebooks" });
        })
        .catch((data) => {
          this.register.isError = true;
          this.register.notice = data.msg;
        });

      console.log(
        `start register..., username: ${this.register.username} , password: ${this.register.password}`
      );
    },
    //登录框
    onLogin() {
      if (!/^[\w\u4e00-\u9fa5]{1,15}$/.test(this.login.username)) {
        this.login.isError = true;
        this.login.notice = "用户名1~15个字符，仅限于字母数字下划线中文";
        return;
      }
      if (!/^.{6,16}$/.test(this.login.password)) {
        this.login.isError = true;
        this.login.notice = "密码长度为6~16个字符";
        return;
      }
      this.login.isError = false;
      this.login.notice = "";

      Auth.login({
        username: this.login.username,
        password: this.login.password,
      })
        .then((data) => {
          this.login.isError = false;
          this.login.notice = "";
          Bus.$emit("userInfo", { username: this.login.username });
          this.$router.push({ path: "notebooks" });
        })
        .catch((data) => {
          this.login.isError = true;
          this.login.notice = data.msg;
        });
    },
  },
};
</script>
<style lang="scss" scoped>

@media (max-width: 500px){
  .modal-wrapper {
  vertical-align: middle;
  .modal-container{
    width:300px;
    display: flex;
    flex-direction: column;
    .main{
      height:160px;
      .icon{
        width: 120px;
    height: 120px;
    margin: 41px 90px;
      }
    }
    .form{
      width:100%;
      border:none;
    }
  }
}
  
}
.modal-mask {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  vertical-align: middle;
}

.modal-container {
  width: 800px;
  height: 500px;
  margin: 48px auto;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans serif;
  display: flex;

  .main {
    flex: 1;
    background: #91c6fc;
    background-size: contain;
    > .icon {
      width: 300px;
      height: 300px;
      margin: 115px;
      color: #fff;
    }
  }
  .form {
    width: 270px;
    border-left: 1px solid #ccc;
    > h3 {
      padding: 10px 20px;
      font-weight: normal;
      font-size: 16px;
      border-top: 1px solid #eee;
      cursor: pointer;
      margin: 0;
    color: #91c6fc;
      &:nth-of-type(1) {
        margin-top: 18px;
      }
      &:nth-of-type(2) {
        border-bottom: 1px solid #eee;
      }
    }
    .button {
      background-color: #91c6fc;
      height: 36px;
      line-height: 36px;
      text-align: center;
      font-weight: bold;
      color: #fff;
      border-radius: 4px;
      margin-top: 18px;
      cursor: pointer;
    }

    .login,
    .register {
      padding: 10px 20px;
      border-top: 1px solid #eee;
      input {
        display: block;
        width: 100%;
        height: 35px;
        line-height: 35px;
        padding: 0 6px;
        border-radius: 4px;
        border: 1px solid #ccc;
        outline: none;
        font-size: 14px;
        margin-top: 10px;
      }
      input:focus {
        border: 3px solid #9dcaf8;
      }
      p {
        font-ize: 12px;
        margin-top: 10px;
        color: #444;
      }
      .error {
        color: red;
      }
    }
    .login {
      border-top: 0;
    }
  }
}
</style>